import React, { useState, useEffect } from "react";
import '../styles/conheca.css';

export default function Conheca() {
    const [conhecaState, setConhecaState] = useState('');
    const [carneState, setCarneState] = useState('');
    const [frutaState, setFrutaState] = useState('');
    const [vuonState, setVuonState] = useState('');
    const [vendaState, setVendaState] = useState('');
    useEffect(() => {
        const scrollHandle = () => {
            if (window.scrollY > 250) {
                setConhecaState('conheca');                
            };
            if (window.scrollY > 500) {
                setCarneState('carne');
            };
            if (window.scrollY > 750) {
                setFrutaState('fruta');
            };
            if (window.scrollY > 1000) {
                setVuonState('vuon');
            };
            if (window.scrollY > 1250) {
                setVendaState('venda');
            };
        }
        window.addEventListener('scroll', scrollHandle);
    }, []);

    return (
        <div className="container-lg">
            <div className="row conheca-title">
                <div className={conhecaState === 'conheca' ? 'col from-left text-center show' : "col from-left text-center"}>
                    <h1>Conhea o que o Fort <span className="red">Faz pra voc</span></h1>
                </div>
            </div>
            <div className="row conheca mt-5">
                <div className={carneState === 'carne' ? "col-md-6 text-center text-md-start from-left show" : 'col-md-6 from-left'}>
                    <img className="img-fluid" src="/fort/images/acougue.png" alt="" />
                </div>
                <div className={carneState == 'carne' ? "col-md-6 text-center text-md-start from-right show" : 'col-md-6 from-right'}>
                    <h1 className="red">Aougue Carne Fresca</h1>
                    <p>Os melhores cortes, sempre fresquinhos. Venha conhecer o melhor do nosso Aougue. Tem dicas, curiosidades, receitas e muito mais.</p>
                    <div className="text-center">
                        <button>VISITAR AOUGUE</button>
                    </div>
                </div>
            </div>
            <div className="row conheca mt-5">
                <div className={frutaState === 'fruta' ? "col-md-6 text-center text-md-start from-left show" : 'col-md-6 from-left'}>
                    <h1 className="red">Frutas, Legumes e Verduras</h1>
                    <p>Parece horta, mas  o nosso FLV. Aqui voc encontra variedade de frutas, legumes e verduras.</p>
                    <div className="text-center">
                        <button>VER HORTIFRUTI</button>
                    </div>
                </div>
                <div className={frutaState === 'fruta' ? "col-md-6 order-first order-md-last text-center text-md-end from-right show" : 'col-md-6 text-end from-right'}>
                    <img className="img-fluid" src="/fort/fort/images/hortifruti.png" alt="" />
                </div>
            </div>
            <div className="row conheca mt-5">
                <div className={vuonState === 'vuon' ? "col-md-6 text-center text-md-start from-left show" : 'col-md-6 from-left'}>
                    <img className="img-fluid" src="/fort/fort/images/vuon.png" alt="" />
                </div>
                <div className={vuonState === 'vuon' ? "col-md-6 text-center text-md-start from-right show" : 'col-md-6 from-right'}>
                    <h1 className="red">Vuon Card</h1>
                    <p>Conhea o Vuon Card, o carto de crdito do Fort. Parcele suas compras, pague a primeira parcela em at 40 dias e tenha controle da sua fatura pelo aplicativo.</p>
                    <div className="text-center">
                        <button>CONHECER O VUON</button>
                    </div>
                </div>
            </div>
            <div className="row conheca mt-5">
                <div className={vendaState === 'venda' ? "col-md-6 text-center text-md-start from-left show" : 'col-md-6 from-left'}>
                    <h1 className="red">Televendas</h1>
                    <p>Empresria e empresrio, a gente tem as melhores solues do tamanho do seu negcio.  variedade e preo baixo que vocs querem? Ento, seja um Parceiro F.</p>
                    <div className="text-center">
                        <button>SER PARCEIRO</button>
                    </div>
                </div>
                <div className={vendaState === 'venda' ? "col-md-6 order-first order-md-last text-center text-md-end from-right show" : 'col-md-6 from-right'}>
                    <img className="img-fluid" src="/fort/images/televendas.png" alt="" />
                </div>
            </div>
        </div>
    );
}
