import React, { useState, useEffect } from "react";
import '../styles/social.css';

export default function Social() {
    const [socialState, setSocialState] = useState('');
    useEffect(() => {
        const scrollHandle = () => {
            if (window.scrollY > 1500) {
                setSocialState('social');
            };            
        }
        window.addEventListener('scroll', scrollHandle);
        return () => window.removeEventListener("scroll", scrollHandle);
    }, []);
    
    return (
        <div className="container-lg mt-5 pt-5">
            <div className="row">
                <div className={socialState === 'social' ? 'col-md-4 social from-left show' : "col-md-4 social from-left"}>
                    <div className="background">
                        <h1>Responsibilidade</h1>
                        <h1>Social</h1>
                        <p>O Fort acredita que por meio de aes que desenvolvam a sociedade em curto e em mdio prazo podem transformar o mundo em um lugar melhor.
                            Por isso, investimos em iniciativas que visam criar um ambiente mais socivel, duradouro e saudvel.
                            Com isso, tambm podemos fortalecer os nossos negcios, nossas vises e valores a cada dia.</p>
                    </div>
                </div>
                <div className={socialState === 'social' ? 'col-md-8 from-right show' : "col-md-8 from-right"}>
                    <div className="row">
                        <div className="col my-5 my-md-0 text-center">
                            <img src="/fort/images/bom-negocio.png" alt="" />
                        </div>
                    </div>
                    <div className="row responsa text-center">
                        <div className="col-md-3">
                            <img src="/fort/images/dosmares.png" alt="" />
                            <button>VER MAIS</button>
                        </div>
                        <div className="col-md-3">
                            <img src="/fort/images/troco-solidario.png" alt="" />
                            <button>VER MAIS</button>
                        </div>
                        <div className="col-md-3">
                            <img src="/fort/images/coleta-de-oleo.png" alt="" />
                            <button>VER MAIS</button>
                        </div>
                        <div className="col-md-3">
                            <img src="/fort/images/coleta-lixo-eletronico.png" alt="" />
                            <button>VER MAIS</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
